const errorsList = {
  required: ' .',
  short: '  6 .',
  space: ' .',
};

export default (stringValue) => {
  if (!stringValue) {
    return {
      valid: false,
      error: errorsList.required,
    };
  }

  if (stringValue.length < 6) {
    return {
      valid: false,
      error: errorsList.short,
    };
  }

  if (stringValue.includes(' ')) {
    return {
      valid: false,
      error: errorsList.space,
    };
  }

  return {
    valid: true,
    error: '',
  };
};
